.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 170%;
  font-weight: 400;
  font-style: normal;
  text-align: left;
  color: #484848;
  background-color: #ffffff;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.scroll-hidden {
  overflow: hidden;
}

.button {
  display: block;
  min-width: 216px;
  padding: 16px 10px 19px 10px;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-size: 21px;
  line-height: 24px;
  font-weight: 500;
  font-style: normal;
  text-align: center;
  color: #ffffff;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.12);
  font-variant: small-caps;
  background-image: linear-gradient(142.39deg, #ffc341 20.46%, #ffd701 65.31%);
  border: none;
  border-radius: 80px;
  cursor: pointer;
  outline: none;
}
.button:hover, .button:focus {
  background-image: linear-gradient(143.55deg, #ffc341 -50.3%, #ffd701 63.1%);
}
.button:active {
  background-image: radial-gradient(150% 119.49% at 50% 50.85%, rgba(0, 0, 0, 0) 30.67%, rgba(3, 3, 3, 0.24) 100%), linear-gradient(143.55deg, #ffc341 -50.3%, #ffd701 63.1%);
}

.input {
  display: block;
  min-width: 250px;
  padding: 19px 10px 16px 40px;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 500;
  font-style: normal;
  text-align: left;
  color: #484848;
  text-transform: uppercase;
  background-color: #ffffff;
  border: 2px solid #e3e3e3;
  border-radius: 80px;
  outline: none;
}
.input::placeholder {
  color: rgba(72, 72, 72, 0.5);
  font-variant: small-caps;
}
.input:hover, .input:focus {
  border-color: #ffc341;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.input:required:valid {
  border-color: #484848;
}
.input--tel-bg {
  background-image: url("../img/input-bg.svg");
  background-position: 55px 38px;
  background-repeat: no-repeat;
}
.input--valid {
  border-color: #484848;
}
.input--invalid {
  border-color: #ff0000;
}

.title {
  margin: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 40px;
  line-height: 49px;
  font-weight: 700;
  color: #1fc0fc;
  text-transform: uppercase;
  letter-spacing: -0.02em;
}

@media (max-width: 767px) {
  .title {
    font-size: 32px;
    line-height: 39px;
  }
}
.social-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.social-list li {
  margin-left: 20px;
}
.social-list li:first-child {
  margin-left: 0;
}

.social-list a {
  display: block;
  width: 24px;
  height: 24px;
}

.social-list svg {
  fill: rgba(0, 0, 0, 0.16);
}
.social-list svg:hover, .social-list svg:focus {
  fill: #ffc341;
}

.list-button__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.list-button li {
  margin-bottom: 4px;
}

.list-button__item {
  position: relative;
  display: block;
  min-width: 228px;
  padding: 26px 10px 24px 72px;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 18px;
  font-weight: 400;
  font-style: normal;
  text-align: left;
  color: rgba(72, 72, 72, 0.87);
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.12);
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}
.list-button__item::before {
  content: "";
  position: absolute;
  display: block;
  background-repeat: no-repeat;
  top: 16px;
  left: 18px;
  width: 36px;
  height: 36px;
}
.list-button__item--general::before {
  background-image: url("../img/icon-star-black@1x.png");
  background-position: 4px 3px;
}
.list-button__item--academic::before {
  background-image: url("../img/icon-hat-black@1x.png");
}
.list-button__item--internship::before {
  background-image: url("../img/icon-bag-black@1x.png");
  background-position: 2px 6px;
}
.list-button__item--volunteer::before {
  background-image: url("../img/icon-hand-black@1x.png");
  background-position: 2px 4px;
}
.list-button__item--religion::before {
  background-image: url("../img/icon-menorah-black@1x.png");
  background-position: 6px 6px;
}
.list-button__item:hover:not(.list-button__item--active), .list-button__item:focus:not(.list-button__item--active) {
  color: #ffc341;
}
.list-button__item--general:hover::before:not(.list-button__item--active), .list-button__item--general:focus::before:not(.list-button__item--active) {
  background-image: url("../img/icon-star-yellow@1x.png");
}
.list-button__item--academic:hover::before:not(.list-button__item--active), .list-button__item--academic:focus::before:not(.list-button__item--active) {
  background-image: url("../img/icon-hat-yellow@1x.png");
}
.list-button__item--internship:hover::before:not(.list-button__item--active), .list-button__item--internship:focus::before:not(.list-button__item--active) {
  background-image: url("../img/icon-bag-yellow@1x.png");
}
.list-button__item--volunteer:hover::before:not(.list-button__item--active), .list-button__item--volunteer:focus::before:not(.list-button__item--active) {
  background-image: url("../img/icon-hand-yellow@1x.png");
}
.list-button__item--religion:hover::before:not(.list-button__item--active), .list-button__item--religion:focus::before:not(.list-button__item--active) {
  background-image: url("../img/icon-menorah-yellow@1x.png");
}
.list-button__item--active {
  font-weight: 500;
  color: #ffffff;
  text-transform: uppercase;
  background: url("../img/ribbon-bg.svg");
  background-repeat: no-repeat;
  background-size: 100%;
}
.list-button__item--active::before {
  filter: invert(100%);
}

@media (max-width: 767px) {
  .list-button {
    width: 100%;
    overflow-x: scroll;
    scrollbar-width: none;
  }
  .list-button__list {
    display: flex;
  }
  .list-button li {
    margin-bottom: 0;
  }
  .list-button__item {
    min-width: auto;
    padding: 16px 12px 14px 52px;
    font-family: "Fira Sans", "Arial", sans-serif;
    font-size: 15px;
    line-height: 18px;
    border-radius: 60.6704px;
    opacity: 0.4;
  }
  .list-button__item::before {
    top: 6px;
    left: 12px;
  }
  .list-button__item--active {
    background-image: linear-gradient(138.15deg, #ffc341 20.46%, #ffd701 65.31%);
    opacity: 1;
  }
}
.accordion {
  font-family: "Fira Sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 21px;
  font-weight: 400;
  font-style: normal;
  text-align: left;
  color: rgba(72, 72, 72, 0.74);
  counter-reset: accordion;
}

.accordion__item {
  min-height: 72px;
  margin-bottom: 24px;
  padding: 13px 0 24px 0;
  border-bottom: 1px solid rgba(72, 72, 72, 0.2);
}

.accordion__button {
  position: relative;
  width: 100%;
  padding: 0 130px 0 28px;
  font-size: 17px;
  line-height: 25px;
  text-align: left;
  color: #484848;
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}
.accordion__button::before {
  content: "";
  position: absolute;
  display: block;
  background-repeat: no-repeat;
  counter-increment: accordion;
  content: counter(accordion);
  top: -15px;
  left: 0;
  font-size: 28px;
  line-height: 48px;
  font-weight: 700;
  color: transparent;
  background: linear-gradient(108.42deg, #ffc341 20.46%, #ffd701 65.31%);
  background-clip: text;
}
.accordion__button::after {
  content: "";
  position: absolute;
  display: block;
  background-repeat: no-repeat;
  top: 8px;
  right: 37px;
  width: 17px;
  height: 11px;
  background-image: url("../img/icon-arrow-accordion.svg");
  background-repeat: no-repeat;
}

.accordion__text {
  height: 0;
  margin: 0;
  overflow: hidden;
}

.accordion__item--active .accordion__button::after {
  transform: rotate(-180deg);
}

.accordion__item--active .accordion__text {
  height: auto;
  padding: 24px 70px 8px 0;
}

@media (max-width: 767px) {
  .accordion__item {
    min-height: 64px;
    padding: 12px 0 16px 0;
  }
  .accordion__button {
    padding: 0 25px 0 25px;
    font-size: 16px;
    line-height: 26px;
  }
  .accordion__button::before {
    top: -13px;
    left: -2px;
  }
  .accordion__button::after {
    top: 10px;
    right: 0;
  }
  .accordion__text {
    font-size: 14px;
    line-height: 22px;
  }
  .accordion__item--active .accordion__text {
    padding: 17px 15px 0 0;
  }
}
.control {
  display: flex;
  justify-content: space-between;
}

.control__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}
.control__button--prev {
  transform: rotate(180deg);
}

.control__button svg {
  width: 11px;
  height: 18px;
  stroke: rgba(72, 72, 72, 0.2);
}

.control__button:hover svg,
.control__button:focus svg {
  stroke: #ffc341;
}

@font-face {
  font-family: "swiper-icons";
  src: url("data:application/font-woff;charset=utf-8;base64, ") format("woff");
  font-weight: 400;
  font-style: normal;
}
:root {
  --swiper-theme-color: #007aff;
  --swiper-navigation-size: 44px;
}

.swiper-container {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  /* Fix of Webkit flickering */
  z-index: 1;
}

.swiper-container-vertical > .swiper-wrapper {
  flex-direction: column;
}

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box;
}

.swiper-container-android .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0, 0, 0);
}

.swiper-container-multirow > .swiper-wrapper {
  flex-wrap: wrap;
}

.swiper-container-multirow-column > .swiper-wrapper {
  flex-wrap: wrap;
  flex-direction: column;
}

.swiper-container-free-mode > .swiper-wrapper {
  transition-timing-function: ease-out;
  margin: 0 auto;
}

.swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
}

.swiper-slide-invisible-blank {
  visibility: hidden;
}

/* Auto Height */
.swiper-container-autoheight,
.swiper-container-autoheight .swiper-slide {
  height: auto;
}

.swiper-container-autoheight .swiper-wrapper {
  align-items: flex-start;
  transition-property: transform, height;
}

/* 3D Effects */
.swiper-container-3d {
  perspective: 1200px;
}

.swiper-container-3d .swiper-wrapper,
.swiper-container-3d .swiper-slide,
.swiper-container-3d .swiper-slide-shadow-left,
.swiper-container-3d .swiper-slide-shadow-right,
.swiper-container-3d .swiper-slide-shadow-top,
.swiper-container-3d .swiper-slide-shadow-bottom,
.swiper-container-3d .swiper-cube-shadow {
  transform-style: preserve-3d;
}

.swiper-container-3d .swiper-slide-shadow-left,
.swiper-container-3d .swiper-slide-shadow-right,
.swiper-container-3d .swiper-slide-shadow-top,
.swiper-container-3d .swiper-slide-shadow-bottom {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 10;
}

.swiper-container-3d .swiper-slide-shadow-left {
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-container-3d .swiper-slide-shadow-right {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-container-3d .swiper-slide-shadow-top {
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-container-3d .swiper-slide-shadow-bottom {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

/* CSS Mode */
.swiper-container-css-mode > .swiper-wrapper {
  overflow: auto;
  scrollbar-width: none;
  /* For Firefox */
  -ms-overflow-style: none;
  /* For Internet Explorer and Edge */
}

.swiper-container-css-mode > .swiper-wrapper::-webkit-scrollbar {
  display: none;
}

.swiper-container-css-mode > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: start start;
}

.swiper-container-horizontal.swiper-container-css-mode > .swiper-wrapper {
  scroll-snap-type: x mandatory;
}

.swiper-container-vertical.swiper-container-css-mode > .swiper-wrapper {
  scroll-snap-type: y mandatory;
}

.swiper-pagination {
  position: absolute;
  text-align: center;
  transition: 300ms opacity;
  transform: translate3d(0, 0, 0);
  z-index: 10;
}

.swiper-pagination.swiper-pagination-hidden {
  opacity: 0;
}

/* Common Styles */
.swiper-pagination-fraction,
.swiper-pagination-custom,
.swiper-container-horizontal > .swiper-pagination-bullets {
  bottom: 10px;
  left: 0;
  width: 100%;
}

/* Bullets */
.swiper-pagination-bullets-dynamic {
  overflow: hidden;
  font-size: 0;
}

.swiper-pagination-bullets-dynamic .swiper-pagination-bullet {
  transform: scale(0.33);
  position: relative;
}

.swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active {
  transform: scale(1);
}

.swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-main {
  transform: scale(1);
}

.swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-prev {
  transform: scale(0.66);
}

.swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-prev-prev {
  transform: scale(0.33);
}

.swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-next {
  transform: scale(0.66);
}

.swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-next-next {
  transform: scale(0.33);
}

.swiper-pagination-bullet {
  width: 8px;
  height: 8px;
  display: inline-block;
  border-radius: 100%;
  background: #000000;
  opacity: 0.2;
}

button.swiper-pagination-bullet {
  border: none;
  margin: 0;
  padding: 0;
  box-shadow: none;
  appearance: none;
}

.swiper-pagination-clickable .swiper-pagination-bullet {
  cursor: pointer;
}

.swiper-pagination-bullet-active {
  opacity: 1;
  background: var(--swiper-pagination-color, var(--swiper-theme-color));
}

.swiper-container-vertical > .swiper-pagination-bullets {
  right: 10px;
  top: 50%;
  transform: translate3d(0, -50%, 0);
}

.swiper-container-vertical > .swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 6px 0;
  display: block;
}

.swiper-container-vertical > .swiper-pagination-bullets.swiper-pagination-bullets-dynamic {
  top: 50%;
  transform: translateY(-50%);
  width: 8px;
}

.swiper-container-vertical > .swiper-pagination-bullets.swiper-pagination-bullets-dynamic .swiper-pagination-bullet {
  display: inline-block;
  transition: 200ms transform, 200ms top;
}

.swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0 4px;
}

.swiper-container-horizontal > .swiper-pagination-bullets.swiper-pagination-bullets-dynamic {
  left: 50%;
  transform: translateX(-50%);
  white-space: nowrap;
}

.swiper-container-horizontal > .swiper-pagination-bullets.swiper-pagination-bullets-dynamic .swiper-pagination-bullet {
  transition: 200ms transform, 200ms left;
}

.swiper-container-horizontal.swiper-container-rtl > .swiper-pagination-bullets-dynamic .swiper-pagination-bullet {
  transition: 200ms transform, 200ms right;
}

/* Progress */
.swiper-pagination-progressbar {
  background: rgba(0, 0, 0, 0.25);
  position: absolute;
}

.swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
  background: var(--swiper-pagination-color, var(--swiper-theme-color));
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  transform: scale(0);
  transform-origin: left top;
}

.swiper-container-rtl .swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
  transform-origin: right top;
}

.swiper-container-horizontal > .swiper-pagination-progressbar,
.swiper-container-vertical > .swiper-pagination-progressbar.swiper-pagination-progressbar-opposite {
  width: 100%;
  height: 4px;
  left: 0;
  top: 0;
}

.swiper-container-vertical > .swiper-pagination-progressbar,
.swiper-container-horizontal > .swiper-pagination-progressbar.swiper-pagination-progressbar-opposite {
  width: 4px;
  height: 100%;
  left: 0;
  top: 0;
}

.swiper-pagination-white {
  --swiper-pagination-color: #ffffff;
}

.swiper-pagination-black {
  --swiper-pagination-color: #000000;
}

.swiper-pagination-lock {
  display: none;
}

/* Scrollbar */
.swiper-scrollbar {
  border-radius: 10px;
  position: relative;
  background: rgba(0, 0, 0, 0.1);
}

.swiper-container-horizontal > .swiper-scrollbar {
  position: absolute;
  left: 1%;
  bottom: 3px;
  z-index: 50;
  height: 5px;
  width: 98%;
}

.swiper-container-vertical > .swiper-scrollbar {
  position: absolute;
  right: 3px;
  top: 1%;
  z-index: 50;
  width: 5px;
  height: 98%;
}

.swiper-scrollbar-drag {
  height: 100%;
  width: 100%;
  position: relative;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  left: 0;
  top: 0;
}

.swiper-scrollbar-cursor-drag {
  cursor: move;
}

.swiper-scrollbar-lock {
  display: none;
}

.swiper-zoom-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.swiper-zoom-container > img,
.swiper-zoom-container > svg,
.swiper-zoom-container > canvas {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.swiper-slide-zoomed {
  cursor: move;
}

.swiper-lazy-preloader {
  width: 42px;
  height: 42px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -21px;
  margin-top: -21px;
  z-index: 10;
  transform-origin: 50%;
  animation: swiper-preloader-spin 1s infinite linear;
  box-sizing: border-box;
  border: 4px solid var(--swiper-preloader-color, var(--swiper-theme-color));
  border-radius: 50%;
