  background-image: url("../img/icon-checkbox-off.png");
  background-repeat: no-repeat;
  background-position: 0 1px;
  background-size: 16px;
  user-select: none;
}

.modal__checkbox:focus ~ label {
  box-shadow: 0 0 2px 1px #ffc341;
}

.modal__checkbox:checked ~ label {
  background-image: url("../img/icon-checkbox-on.png");
}

.modal__close {
  position: absolute;
  top: 21px;
  right: 21px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}

.modal__close svg {
  fill: #ffc341;
}

.modal__close:hover svg,
.modal__close:focus svg {
  fill: #484848;
}

@media (max-width: 767px) {
  .modal__card {
    width: 100%;
    height: 100vh;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0;
    box-shadow: none;
  }
  .modal__card--success {
    background-position: center 203px;
  }
  .modal__wrapper {
    width: 288px;
  }
  .modal__title {
    margin-bottom: 17px;
    font-size: 24px;
    line-height: 29px;
    text-align: center;
  }
  .modal__text {
    margin-bottom: 31px;
    font-size: 14px;
    line-height: 22px;
    text-align: center;
  }
  .modal__form-wrapper {
    margin: 0 auto;
  }
  .modal__close {
    position: absolute;
    top: 10px;
    right: 10px;
  }
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
}